import NavbarComponent from './Components/NavbarComponent';
import {BrowserRouter, Route, Switch } from 'react-router-dom';
import HomeComponent from './Components/HomeComponent';
import AboutComponent from './Components/AboutComponent';
import EventsComponent from './Components/EventsComponent';
import ServicesComponent from './Components/ServicesComponent';
import BooksComponent from './Components/BooksComponent';
import PhotosComponent from './Components/PhotosComponent';
import VideosComponent from './Components/VideosComponent';
import ContactUsComponent from './Components/ContactUsComponent';
import DonationComponent from './Components/DonationComponent';
import FooterComponent from './Components/FooterComponent';

function App() {
  return (
    <BrowserRouter>
    <div className="App">
      <NavbarComponent />
       
          <Switch>
            <Route exact path = "/" component= {HomeComponent} />
            <Route exact path = "/about" component= {AboutComponent} />
            <Route exact path = "/events" component= {EventsComponent} />
            <Route exact path = "/services" component= {ServicesComponent} />
            <Route exact path = "/books" component= {BooksComponent} />
            <Route exact path = "/photos" component= {PhotosComponent} />
            <Route exact path = "/videos" component= {VideosComponent} />
            <Route exact path = "/contact" component= {ContactUsComponent} />
            <Route exact path = "/donate" component= {DonationComponent} />
          </Switch>
      <FooterComponent />
    </div>
    </BrowserRouter>
  );
}

export default App;
